import React, { Component } from 'react';
import { HeaderStyled, LogoStyled, NavStyled } from './styles';
import {Link, NavLink} from 'react-router-dom';
import './index.css'




const Header = () => {
    return (
        <HeaderStyled>
            <div className='container d-flex justify-content-between align-items-center'>
                <LogoStyled>
                    <Link to='/'>
                    
                        <span><i class="fas fa-file-video"></i> BLOCKMOVIES</span>
                    </Link>
                </LogoStyled>
                <NavStyled>
                    <ul className="nav justify-content-center">
                        <li className="nav-item">
                            <NavLink activeClassName="active" to='/sobre' className="nav-link" >SOBRE</NavLink>
                        </li>
                        <li className="nav-item">
                            <Link to='/filmes' className="nav-link">FILMES</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to='/painel'>PAINEL</Link>
                        </li>
                        <li className="nav-item">
                            <Link className="nav-link" to='/contato'>CONTATO</Link>
                        </li>
                    </ul>
                </NavStyled>
                <NavStyled>
                    <ul className="nav justify-content-center">
                        <li className="nav-item">
                            <a className="nav-link" href="#"><i class="fab fa-facebook"></i></a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#"><i class="fab fa-instagram"></i></a>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#"><i class="fab fa-github"></i></a>
                        </li>
                    </ul>
                </NavStyled>
               </div>
        </HeaderStyled>
            )
        }
export default Header;