import React, { Component } from 'react';
import firebase from '../../fireconnection';
import validator from 'validator';
import Alerta from '../../Utils/Alertas';
import { AlertaSucess, AlertaDanger } from '../../Utils/AlertaStyled';
import SelectCategorias from '../../Components/Categorias/SelectCategorias';
import { throwStatement } from '@babel/types';

class PainelDeCadastroFilmes extends Component {
    constructor(props) {
        super(props);
        this.stateInicial = {
            titulo: '',
            sinopse: '',
            capa: '',
            video: '',
            categoria: '',
        }
        this.state = { check: false }
        this.state = this.stateInicial;
        this.cadastrar = this.cadastrar.bind(this);
        this.limpaFormulario = this.limpaFormulario.bind(this)
        this.voltarMenu = this.voltarMenu.bind(this);
        this.getCategoria = this.getCategoria.bind(this);
        
        
        
    }
    limpaFormulario() {
        this.setState(this.stateInicial)
    }

    cadastrar(e) {
        e.preventDefault();
        let Filmes = firebase.database().ref('Filmes');
        let check = true;
        Filmes.once('value', (snapshot) => {
            snapshot.forEach((item) => {

                if ((item.val().id === this.state.id) || (validator.equals(item.val().titulo, this.state.titulo))) {
                    Alerta.ShowAlertDanger('alertacamposigual');
                    check = false;
                    this.limpaFormulario();

                }
            })

        })
        if (validator.isURL(this.state.capa) || validator.isURL(this.state.video)) {
            check = true;
        } else {
            check = false;
            Alerta.ShowAlertDanger('alertacamposurl');
            this.limpaFormulario();
        }
        if(this.state.categoria === ''){
            check= false;
            Alerta.ShowAlertDanger('falhacategoria');
        }else{
            check=true;
        }
        if (check) {
            let chave = this.state.id;
            Filmes.child(chave).set({
                titulo: this.state.titulo,
                sinopse: this.state.sinopse,
                capa: this.state.capa,
                video: this.state.video,
                id: this.state.id,
                categoria: this.state.categoria
            })
            Alerta.ShowAlertSucess('alertasucess');
            this.limpaFormulario();
        }
        
        firebase.database().ref('Filmes').limitToLast(1).on('value',(snapshot)=>{
            snapshot.forEach((item)=>{
                let state = this.state;
                state.id = item.val().id
                let proximoID = parseInt(state.id,10);
                ++proximoID;
                state.id = proximoID.toString();
                this.setState(state)
            })
            
            
           
        })
        this.limpaFormulario();
    }
    voltarMenu() {
        this.props.history.push('/painel/menu')
    }
    getCategoria(valor){
       let state = this.state;
       state.categoria = valor;
        
        this.setState(state)
        
        
    }
    componentDidMount() {
        firebase.auth().onAuthStateChanged((user) => {
            if (user) {
                this.setState({ check: true })

            }
        })
        firebase.database().ref('Filmes').limitToLast(1).on('value',(snapshot)=>{
            snapshot.forEach((item)=>{
                let state = this.state;
                state.id = item.val().id
                let proximoID = parseInt(state.id,10);
                ++proximoID;
                state.id = proximoID.toString();
                this.setState(state)
            })
            
            
           
        })
       
        
    }
    render() {
        return (

            <div>
                {
                    this.state.check &&
                    (
                        <div className='container painel-cadastrar-container '>
                            <h1>Cadastrar Filmes</h1>
                            <AlertaDanger id='falhacategoria'>Por favor selecione uma categoria!</AlertaDanger>
                            <AlertaDanger id='alertacamposurl'>O video e capa precisam estar no formato de link(URL)!</AlertaDanger>
                            <AlertaDanger id='alertacamposigual'>Id ou Titulo cadastrado igual!</AlertaDanger>
                            <AlertaSucess id='alertasucess'>Filme cadastrado com sucesso!</AlertaSucess>
                            <form class="form-group painel-cadastrar" onSubmit={this.cadastrar}>
                                <label for="titulo">Titulo:</label>
                                <input type="text" name="titulo" id="titulo" required class="form-control" placeholder="Digite o titulo do filme:" aria-describedby="helpId" value={this.state.titulo} onChange={(e) => this.setState({ titulo: e.target.value })} />
                                <label for="sinopse">Sinopse:</label>
                                <input type="text" name="sinopse" id="sinopse" required class="form-control" placeholder="Digite a sinopse do filme:" value={this.state.sinopse} aria-describedby="helpId"
                                    onChange={(e) => this.setState({ sinopse: e.target.value })} />
                                <SelectCategorias value={this.getCategoria}/>
                                <label for="capa">Capa:</label>
                                <input type='link' name="capa" id="capa" class="form-control" required placeholder="Coloque o link da capa do filme" value={this.state.capa} aria-describedby="helpId"
                                    onChange={(e) => this.setState({ capa: e.target.value })} />
                                <label for="video">Video:</label>
                                <input type="text" name="video" id="video" class="form-control" required placeholder="Coloque o link do video" value={this.state.video} aria-describedby="helpId"
                                    onChange={(e) => this.setState({ video: e.target.value })} />
                                <label for="id">ID:</label>
                                <input type="number" name="id" id="id" class="form-control" required placeholder='Digite o ID do filme' aria-describedby="helpId" value={this.state.id}
                                     />
                                
                                <button type='submit' name="cadastrar" id="cadastrar" class="botao" role="button">Cadastar</button>
                                <a name="voltar" id="voltar" class="botao" href="#" role="button" onClick={this.voltarMenu}>Voltar ao menu</a>
                            </form>

                        </div>
                    )
                }

            </div>
        )
    }

}
export default PainelDeCadastroFilmes;



