import React,{Component} from 'react';
import firebase from '../../fireconnection';
import {PostMovie} from './styled';
import $ from 'jquery';
import ComentarioBox from '../../Components/ComentarioBox';


class Filme extends Component {
    constructor(props){
        super(props);
        this.state={
           listaComentarios:[]
        }
        this.showSinopse = this.showSinopse.bind(this);
      
        this.movie = '';
        this.showComentarios = this.showComentarios.bind(this);
        $('#teste').hide();
       
    }
    componentDidUpdate(){
        
    }
    componentDidMount(){
        const {id} = this.props.match.params;
        firebase.database().ref('Filmes').child(`${id}`).on('value',(snapshot)=>{
            let newFilme = {
                titulo:snapshot.val().titulo,
                sinopse:snapshot.val().sinopse,
                capa:snapshot.val().capa,
                video:snapshot.val().video,
                id:snapshot.val().id,
                categoria:snapshot.val().categoria
            }
            let state = this.state;
            state = newFilme
            this.movie = snapshot.val().titulo;
            this.setState(state);
            
            firebase.database().ref('Comentarios').child(`${snapshot.val().titulo}`).once('value',(snapshot)=>{
                snapshot.forEach((comentario)=>{
                    let state = this.state;
                    state.listaComentarios.push({
                        nome:comentario.val().nome,
                        email:comentario.val().email,
                        comentario:comentario.val().comentario
                    })
                    this.setState(state);
                    console.log(state);
                    
                })
            })
            
          
            
          
            
        })
        
        $('#sinopse').hide();
       
    }
    showSinopse(){
        $('#sinopse').slideToggle();
    }
    showComentarios(){
        $('#teste').slideToggle();
        console.log(this.state.titulo)
        
    }
    render(){
        return(
            <div className='container'>
                <PostMovie>
                    
                    <h3>{this.state.titulo}</h3>
                    <img className='img-fluid' src={this.state.capa}/>
                    <iframe width="560" height="315" src={this.state.video} frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen='1'></iframe>
                    <h4>Sinopse <i class="fas fa-arrow-circle-down"onClick={this.showSinopse}></i> </h4>
                    <h5 id='sinopse'>"{this.state.sinopse}"</h5>
                    <div className='comentarios'>
                    
                    
                    <ComentarioBox filme={this.movie}/>
                    </div>
                    <h4>Comentários <i class="fas fa-arrow-circle-down" onClick={this.showComentarios}></i></h4>
                   <div id='teste'>
                    {
                        this.state.listaComentarios.map((comentario)=>{
                            return(
                                            <div  className='lista-comentarios'>
                                            <h5> <span>Nome:</span> {comentario.nome}<br/><span>Email:</span> {comentario.email}</h5>
                                            <h6><span>Diz:</span> "{comentario.comentario}"</h6>
                                            </div>
                              
                            )
                        })
                    }
                    </div>
                    
                    
                </PostMovie>
            </div>
        )
    }
}
export default Filme;